@tailwind base;
@tailwind components;
@tailwind utilities;

/* 自定义样式 */
.human-msg {
  background-color: #fffde7;
}
.claude-msg {
  background-color: #e3f2fd;
}
.selected-msg {
  background-color: #bbdefb !important;
}

/* 修复布局问题 */
.detail-content-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.json-content {
  width: 100%;
  overflow-x: auto;
}

/* 确保主要内容区域不会被挤压 */
.flex-1 {
  flex: 1 1 0% !important;
}

/* 确保消息列表面板有一个固定的最小宽度 */
.message-list-panel {
  min-width: 350px;
  width: 40%;
  max-width: 500px;
  flex-shrink: 0;
}

/* JSON选项卡的特殊处理 */
.json-tab-content {
  max-width: 100%;
  overflow-x: auto !important;
  overflow-y: auto !important;
}

/* 提高JSON代码块的可读性 */
pre[class*="language-json"] {
  white-space: pre-wrap !important;
  word-break: break-word !important;
  max-width: 100% !important;
}

/* 确保右侧内容区域不会被挤压 */
.content-panel {
  min-width: 0;
  flex: 1 1 auto;
  overflow: hidden;
}

/* 修复选项卡样式 */
.tab-button {
  flex-shrink: 0;
}

/* 解决某些界面元素的固定宽度问题 */
.fixed-width-input {
  width: auto !important;
  flex: 1;
}

/* 确保代码高亮区域的限制 */
.syntax-highlighter-container {
  max-width: 100%;
  overflow-x: auto;
}

/* 确保所有标签面板具有一致的最小高度 */
.tab-content-container {
  min-height: 300px;
  display: flex;
  flex-direction: column;
}

/* 设置所有内容区域统一样式 */
.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
  background: white;
  border: 1px solid #e5e7eb;
  border-top: none;
  border-radius: 0 0 0.25rem 0.25rem;
}

/* 确保内部内容不会导致高度变化 */
.metadata-panel,
.export-panel,
.tools-panel {
  min-height: 300px;
  display: flex;
  flex-direction: column;
}

/* 底部面板容器 - 修改为可条件渲染 */
.panel-content-container {
  min-height: 330px;
  transition: all 0.3s ease;
}

/* 各个面板内容区域统一样式 */
.panel-content-area {
  min-height: 260px;
}

/* 修复分支管理器样式 */
.branch-manager {
  border-top: 1px solid #e5e7eb;
  padding: 0.5rem;
  background-color: #f9fafb;
}

/* 添加底部状态栏样式 */
.status-bar {
  background-color: #f9fafb;
  border-top: 1px solid #e5e7eb;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 底部工具栏的收起/展开动画 */
.bottom-panel-toggle {
  transition: transform 0.2s ease;
}

.bottom-panel-toggle.collapsed {
  transform: rotate(180deg);
}

/* 底部选项卡导航栏 */
.bottom-tabs-nav {
  display: flex;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

/* 底部快捷按钮区域 */
.bottom-action-buttons {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

/* 收起面板时的状态栏样式增强 */
.status-bar-expanded {
  padding: 0.75rem 1rem;
}

/* 修复面板显示/隐藏时的布局跳动 */
.main-content-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}